.footer {
  height: 50px;
  background-color: #2c3e50;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 -4px 4px rgba(0, 0, 0, 0.25);
}

.footer-text {
  text-transform: uppercase;
  font-family: 'Titillium Web', sans-serif;
  color: #fff;
  font-size: 1rem;
}

@media only screen and (max-width: 480px) {
  /* Mobile phones */
  .footer-text {
    font-size: 0.8rem;
  }
}
